.join-us-btn {
    width: 150px;
    height: 50px;
    line-height: 50px;
    border: none;
    border-radius: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    color: var(--white);
    background-color: var(--join-us-btn);
    justify-self: flex-end;
}

.join-us-btn:hover {
    border: none;
}