    text-align: left;
}

.footerImg{
    display: flex;
    justify-content: space-around;
}

.footerImg img {
    width: 40px;
}
.scrollTop {
  position: fixed; 
  width: 100%;
  bottom: 20px;
  right: -40%;
  align-items: center;
  height: 20px;
  justify-content: center;
  z-index: 1000;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.8;
}

.scrollTop:hover{
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
/*reseting the styles*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --logo: #0A2140;
  --section-que-hacemos-bg-color:#C2E5F2;
  --section-solucion-bg-color:#7EC8D9;
  --section-metodologia-bg-color:#1D72A7;
  --section-contactanos-bg-color:#C2E5F2;
  --footer-bg-color:#037691;
  --acceder-btn-bg-color:#2C7373;
  --intranet-bg-color:#95BBCE;
  --text-color:#38A0C7;
  --white: #ffffff;
  --join-us-btn: #0A2140;
}

img {
  width: 100%;
  display: block;
  height: auto;
}

a, a:hover {
  color: inherit;
  text-decoration: inherit;
}

/*main styles*/
body {
  font-family: 'Poppins', sans-serif;
  color: #000;
  line-height: 1;
  font-weight: 400;
  margin: 0 auto;
  font-size:15px;
  line-height:1.6;
  overflow-x: hidden;
}

.fredokaStyle,h1,h2 {
  font-family: 'Fredoka One', cursive;
}

/*sizes*/
h1 { font-size: 2.2rem;}
h2 { font-size: 2rem;}
h3 { font-size: 1.6rem; }
h4 { font-size: 1.2rem; }
h5 { font-size: 1rem; }

p {
font-size: 1em;
}

@media (max-width: 576px) { 
  .logo {
    text-align: center;
  } 
}

@media screen and (max-width:600px){
  body {font-size:16px;}
  h1 { font-size: 2rem;}
  h2 { font-size: 1.9rem; }
  h3 { font-size: 1.7rem; }
  h4 { font-size: 1.7rem; }
  h5 { font-size: 1rem; }
  footer {
    width: 100%;
  }

  /* hero */
  .hero h1 {
    font-size: 1rem;
    margin-top: 5%;
  }

  /* joinUs box */
  .join-us {
    height: auto;
    box-shadow: none;
    background-color: none;
    transform: translateY(0);
  }

  /* porQue */
  .porQueJumbotronP{
    text-align: left;
    padding-left: 2%;
  }

  /* solution */
  .solution-text {
    padding-left: 2%;
  }

  /* methodology */
  .upSchema {
    flex-direction: column;
    justify-content: center;
  }

  /*mainform*/
  .centerForm {
    max-width: 100%;
  }

  .formInput {
    width: 100%;
  }

  /* footer */
  .footerRow * {
    margin-bottom: 5%;
  }

  .adressFooter {
    text-align: center;
  }

  .upSchema article, .downSchema article {
    width: 100%;
    margin-bottom: 5%;
  }

  /* twoCards */
  .eachcards {
    width:80%;
  }

  .eachcards:first-of-type {
    margin-bottom:10%;
  }
}

@media screen and (min-width:768px){
  body{font-size:18px;}
  h1 { font-size: 2.6rem;}
  h2 { font-size: 2.5rem; }
  h3 { font-size: 2rem; }
  h4 { font-size: 1rem; }
  h5 { font-size: 1rem; }

  .porQueJumbotronP {
    max-width: 70%;
  }

  .join-us {
    background-color: none;
    transform: translateY(-125%);
  }
}

@media screen and (min-width:768px) and (max-width: 1050px) {
  .join-us {
    width: 100%;
    transform: translateY(-100%);
  }
} 

@media screen and (max-width:768px){
  .footerLogoDisplay {
    display: none;
  }

  /*main form*/
  .centerForm{
    max-width: 80%;
  }

  .formInput{
    width: 100%;
  }
}

@media screen and (min-width:992px){
  body{
    font-size:20px;
  }
  
  .sectionChartText {
    max-width: 40%;
    margin-right: 5%;
  }

  .join-us {
    transform: translateY(-110%);
  }
}
